.title {
    font-weight: 200;
    text-transform: uppercase;
    text-align: center;
    font-size: 48px;
}



.ul {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    align-items: center;
    list-style-type: none;
}


.li {  
    font-size: 34px;
    color: #000000;
    border: 2px solid #ccc;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding: 25px;
    text-align: center;
    margin: 30px;
    border-radius: 20px;
}


.link {

    text-decoration: none; 
}


.input {
    padding: 10px 20px;
    font-size: 18px;
    margin: 20px;
    display: flex;
    justify-content: center;
}


.button {
    padding: 10px 40px;
    border-radius: 20px 20px;
    font-size: 16px;
    background-color: rgb(15, 152, 216);
}